<template>
  <chart :options="option" ref="chartsInstall"/>
</template>

<script>
  import CommonWrapper from '@/components/commonWrapper'
  import ECharts from 'vue-echarts/components/ECharts'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title'

  export default {
    name: 'chartsInstall',
    mounted () {
          this.selfAdaption ()
    },
    data () {
      return {
       option: {
            color: ['#3EAFF7','#F5B366','#4FDAD2','#F3D38C','#C9F0F4'],
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'cross',
                    crossStyle: {
                        color: '#999'
                    }
                }
            },
            toolbox: {
                feature: {
                    dataView: {show: true, readOnly: false},
                    magicType: {show: true, type: ['line', 'bar']},
                    restore: {show: true},
                    saveAsImage: {show: true}
                }
            },
            legend: {
                data:[ '收货总数','待收货','已收货']
            },
            xAxis: [
                {
                    type: 'category',
                    show:false,
                    data: [],
                    axisPointer: {
                        type: 'shadow'
                    }
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    show:false,
                    // name: '总计',
                    // min: 0,
                    // max: 250,
                    // interval: 50,
                    axisLabel: {
                        formatter: '{value}'
                    }
                },
                // {
                //     type: 'value',
                //     show:false,
                //     name: '完成',
                //     min: 0,
                //     max: 25,
                //     interval: 5,
                //     axisLabel: {
                //         formatter: '{value} %'
                //     }
                // }
            ],
            series: [
                {
                    name:'收货总数',
                    type:'bar',
                    barMaxWidth:'30',
                    data:[]
                },
                {
                    name:'待收货',
                    type:'bar',
                    barMaxWidth:'30',
                    data:[]
                },
                {
                    name:'已收货',
                    type:'bar',
                    barMaxWidth:'30',
                    data:[]
                }
            ]
        }
      }
    },
    props: {
      getSeries: {
        type: Array
      }
    },
    watch: {
      getSeries(newVal) {
        if(newVal && newVal.length >0){
          this.option.xAxis[0].data = []
          this.option.series[0].data = []
          this.option.series[1].data = []
          this.option.series[2].data = []
          this.option.xAxis[0].show = true
          this.option.yAxis[0].show = true
          newVal.map(item=>{
            this.option.xAxis[0].data.push(item.brand)
            this.option.series[0].data.push(item.rcvProductTotal)
            this.option.series[1].data.push(item.waitRcvProduct)
            this.option.series[2].data.push(item.recProductCount)
          })
        }else{
          this.option.xAxis[0].data = []
          this.option.series[0].data = []
          this.option.series[1].data = []
          this.option.series[2].data = []
          this.option.xAxis[0].show = false
          this.option.yAxis[0].show = false
        }
      }
    },
    components: { CommonWrapper, chart: ECharts },
    methods:{
       selfAdaption ()  {
        let _this = this ;
        setTimeout(() => {
            window.addEventListener('resize', function () {
                _this.$refs.chartsInstall.resize();
            })
          }, 10)
      }
    }
  }
</script>

<style scoped>

</style>



